module.exports = "\n\nbody {\n    background: #f39c12;\n  }\n  .center-on-page {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    -webkit-transform: translate(-50%,-50%);\n            transform: translate(-50%,-50%);\n  }\n  h1 {\n    text-align: center;\n  }\n  /* Reset Select */\n  select {\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    -ms-appearance: none;\n    appearance: none;\n    outline: 0;\n    box-shadow: none;\n    border: 0 !important;\n    background: #e7e5e4;\n    font-size: 16px;\n    background-image: none;\n  }\n  /* Custom Select */\n  .select {\n    position: relative;\n    display: block;\n    width: 20em;\n    height: 3em;\n    line-height: 3;\n    background: #2c3e50;\n    overflow: hidden;\n    border-radius: .25em;\n  }\n  select {\n    width: 100%;\n    height: 100%;\n    margin: 0;\n    padding: 0 0 0 .5em;\n    color: rgb(17, 75, 36);\n    cursor: pointer;\n  }\n  select::-ms-expand {\n    display: none;\n  }\n  /* Arrow */\n  .select::after {\n    content: '\\25BC';\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    padding: 0 1em;\n    background: #34495e;\n    pointer-events: none;\n  }\n  /* Transition */\n  .select:hover::after {\n    color: #f39c12;\n  }\n  .select::after {\n    transition: .25s all ease;\n  }\n  /*Button*/\n  .button {\n    background-color: #4CAF50; /* Green */\n    border: none;\n    color: white;\n    padding: 15px 32px;\n    text-align: center;\n    text-decoration: none;\n    display: inline-block;\n    font-size: 16px;\n    margin: 4px 2px;\n    cursor: pointer;\n  }\n  .button1 {\n    background-color: white; \n    color: black; \n    border: 2px solid #4CAF50;\n    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);\n  }\n  .button2 {\n    background-color: rgb(241, 155, 155); \n    color: black; \n    border: 2px solid #008CBA;\n  }\n  .button3 {\n    background-color: rgb(221, 125, 125); \n    color: black; \n    border: 2px solid #f44336;\n    padding: 10px 10px;\n  }\n  .button3:hover {\n    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);\n  }\n  .button4 {\n    background-color: white;\n    color: black;\n    border: 2px solid #e7e7e7;\n  }\n  .button5 {\n    background-color: white;\n    color: black;\n    border: 2px solid #555555;\n  }\n  /*Tabla de productos*/\n  table.blueTable {\n    border: 1px solid #1C6EA4;\n    background-color: #EEEEEE;\n    width: 100%;\n    text-align: center;\n    border-collapse: collapse;\n  }\n  table.blueTable td, table.blueTable th {\n    border: 1px solid #AAAAAA;\n    padding: 3px 2px;\n  }\n  table.blueTable tbody td {\n    font-size: 16px;\n  }\n  table.blueTable tr:nth-child(even) {\n    background: #D0E4F5;\n  }\n  table.blueTable thead {\n    background: #1C6EA4;\n    background: linear-gradient(to bottom, #5592bb 0%, #327cad 66%, #1C6EA4 100%);\n    border-bottom: 2px solid #444444;\n  }\n  table.blueTable thead th {\n    font-size: 16px;\n    font-weight: bold;\n    color: #FFFFFF;\n    border-left: 2px solid #D0E4F5;\n  }\n  table.blueTable thead th:first-child {\n    border-left: none;\n  }\n  table.blueTable tfoot {\n    font-size: 20px;\n    font-weight: bold;\n    color: rgb(0, 20, 201);\n    background: #D0E4F5;\n    background: linear-gradient(to bottom, #dcebf7 0%, #d4e6f6 66%, #D0E4F5 100%);\n    border-top: 2px solid #444444;\n  }\n  table.blueTable tfoot td {\n    font-size: 16px;\n  }\n  table.blueTable tfoot .links {\n    text-align: right;\n  }\n  table.blueTable tfoot .links a{\n    display: inline-block;\n    background: #1C6EA4;\n    color: #FFFFFF;\n    padding: 2px 8px;\n    border-radius: 5px;\n  }\n/*# sourceMappingURL=data:application/json;base64, */"